* {
  margin: 0;
  padding: 0;
}

:root {
  --pink: #e86f54;
  --white: #fff;
  --black: #000;
  --grey: #5c5c5c;
  --light-grey: #d7d7d7;
}

.container {
  max-width: 767px;
  margin: 0 auto;
  padding: 0 16px;
}

.list {
  list-style-type: none;
}

.section {
  padding: 100px 0;
}

.link {
  text-decoration: none;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 1279px;
    padding: 0 24px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 28px;
  }
}

.logo {
  width: 45px;
  height: 35px;
}

.phone {
  width: 20px;
  height: 20px;
}

.header-flex {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.nav-flex {
  display: none;
}

.menu-toggle {
  cursor: pointer;
  background-color: #0000;
  border: none;
}

.header-link {
  color: var(--black);
  text-decoration: none;
  display: inline-block;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--pink);
  z-index: 1;
  flex-direction: column;
  justify-content: center;
  padding: 32px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.close-btn {
  width: 24px;
  height: 24px;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 22px;
  right: 22px;
}

.close-icon {
  stroke: var(--white);
}

.mobile-menu {
  color: var(--white);
  flex-direction: column;
  align-items: center;
  gap: 26px;
  font-family: montserrat, sans-serif;
  font-size: 20px;
  line-height: 1.2em;
  display: flex;
}

.mobile-link {
  color: var(--white);
  text-decoration: none;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .nav-flex {
    display: block;
  }

  .menu-toggle {
    display: none;
  }

  .tablet-menu {
    gap: 24px;
    font-family: montserrat, sans-serif;
    font-size: 20px;
    line-height: 1.2em;
    display: flex;
  }

  .logo {
    width: 72px;
    height: 57px;
  }

  .phone {
    width: 22px;
    height: 22px;
  }
}

@media screen and (min-width: 1280px) {
  .header-link {
    color: var(--white);
  }

  .logo, .phone {
    fill: var(--white);
  }
}

.about-title {
  color: var(--black);
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.about-section {
  flex-direction: row;
  align-items: center;
  gap: 14px;
  margin-bottom: 32px;
  display: flex;
}

.line-section {
  width: 100%;
  height: 1px;
  background-color: var(--black);
}

.flex-about {
  max-width: 300px;
  flex-direction: column;
  gap: 16px;
  margin: auto;
  display: flex;
}

.about-text {
  width: 288px;
  color: var(--black);
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

@media screen and (min-width: 768px) {
  .about-title {
    font-size: 44px;
  }

  .about-section {
    flex-direction: row;
    align-items: center;
    display: flex;
  }

  .about-text {
    width: 322px;
  }

  .flex-about {
    max-width: 730px;
    flex-direction: row;
    gap: 54px;
    margin: auto;
    display: flex;
  }

  .about-text {
    padding-top: 26px;
    font-size: 20px;
  }

  .about-photo-size {
    width: 344px;
    height: 217px;
  }
}

@media screen and (min-width: 1158px) {
