    width: 632px;
    height: 344px;
  }

  .about-text {
    width: 510px;
    font-size: 22px;
  }

  .flex-about {
    max-width: 1220px;
    justify-content: space-between;
    align-items: center;
  }
}

.section-delivery {
  flex-direction: row;
  align-items: center;
  gap: 14px;
  margin-bottom: 21px;
  display: flex;
}

.delivery-title {
  color: var(--black);
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.delivery-photo {
  margin-top: 40px;
}

.delivery-list-main {
  color: var(--black);
  letter-spacing: .02em;
  text-align: left;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.5;
  list-style: none;
}

.delivery-list {
  align-items: center;
  margin-top: 25px;
  display: flex;
}

.delivery-number {
  color: var(--black);
  width: 40px;
  height: 40px;
  border: 1px solid #000;
  border-radius: 50%;
