  align-items: center;
  margin-right: 16px;
  padding: 16px;
  font-size: 20px;
  font-weight: 500;
  display: flex;
}

.hero-section {
  text-align: center;
  margin-top: 44px;
  margin-bottom: 100px;
}

.hero-flex {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

.hero-elements-flex {
  align-items: center;
  gap: 167px;
  display: flex;
  position: absolute;
  top: 92px;
}

.hero-title {
  width: 46px;
  height: 34px;
  font-family: montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.07px;
}

.hero-main-title {
  text-align: center;
  font-family: montserrat, sans-serif;
  font-size: 52px;
  font-weight: 400;
  line-height: 63.39px;
  position: absolute;
  top: 247px;
}

.shop-svg {
  width: 64px;
  height: 76px;
}

.shop-btn {
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero-background {
  width: 145px;
  height: 274px;
  background-image: url("flower_homepage@1x.1fdad10a.jpg");
  background-position: center;
  background-size: cover;
  border-radius: 226px;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
