    background-image: url("flower_homepage@2x.337f50fd.jpg");
  }
}

@media screen and (min-width: 768px) {
  .hero-section {
    margin-top: 36px;
  }

  .hero-background {
    width: 390px;
    height: 636px;
  }

  .hero-elements-flex {
    gap: 480px;
    top: 262px;
  }

  .hero-main-title {
    font-size: 120px;
    line-height: 146.28px;
    top: 584px;
  }

  .shop-svg {
    width: 145px;
    height: 150px;
  }

