@media screen and (min-width: 1280px) {
  .hero-background {
    display: none;
  }

  .header-flex {
    z-index: 1;
    background-color: #0000;
    position: relative;
  }

  .hero-section {
    width: 100%;
    height: 878px;
    background-image: url("flower_homepage@1x.1fdad10a.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto;
    margin-top: 0;
    position: relative;
    bottom: 76px;
  }

  .hero-elements-flex {
    gap: 725px;
    top: 555px;
  }

  .hero-title {
    color: var(--white);
    font-size: 32px;
    line-height: 39px;
  }

  .hero-main-title {
    color: var(--white);
    font-size: 160px;
    line-height: 195px;
    top: 328px;
  }

  .shop-svg {
    width: 166px;
    height: 166px;
    fill: var(--white);
  }
}

.flex-store {
