  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.store-item-price {
  color: var(--black);
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.btn-order-style {
  color: var(--white);
  background-color: var(--pink);
  cursor: pointer;
  min-width: 138px;
  min-height: 24px;
  border: none;
  border-radius: 38px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 32px auto 60px;
  padding: 14px 28px;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
