}

.order-flower-box {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .store-photo-size {
    width: 344px;
    height: 399px;
  }

  .btn-order-style {
    height: 64px;
    padding: 16px 32px;
  }

  .store-wrapper {
    max-width: 768px;
  }

  .store-item-name {
    width: 100%;
    height: 68px;
    font-size: 36px;
  }

  .line-wrap {
    max-width: 344px;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .store-item-price {
    font-size: 24px;
  }

  .btn-order-style {
    height: 64px;
    padding: 16px 32px;
  }

  .line-store {
    width: 50px;
    height: 1px;
    background-color: #000;
  }
}

@media screen and (min-width: 1158px) {
  .flex-store {
    columns: 2;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .store-wrapper {
    max-width: 1220px;
  }

  .line-wrap {
    max-width: 600px;
    display: flex;
  }

  .store-photo-size {
    width: 600px;
    height: 542px;
  }

  .store-item-price {
    font-size: 28px;
  }

  .line-store {
    width: 100%;
  }
}

.footer-container {
