.backdrop {
  width: 100%;
  height: 100%;
  background-color: #2e2f4266;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 90%;
  min-height: 380px;
  background: #fff;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 1px #00000024, 0 1px 3px #0000001f, 0 2px 1px #0003;
}

.modal-header {
  width: 100%;
  height: 272px;
  background-image: url("bouquet-newsletter@1x.639e39f3.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  padding: 0;
  position: relative;
}

.modal-background-image {
  width: 100%;
  background-size: contain;
  margin-top: 0;
  display: block;
}

.close-button {
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.icon-x {
  stroke: #000;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-content {
  margin-bottom: 28px;
  margin-left: 16px;
  margin-right: 16px;
}

.title-modal {
  text-align: center;
  letter-spacing: .02em;
  color: #000;
  margin-bottom: 16px;
  font-family: Montserrat, sans-serif;
  font-size: 19px;
  font-weight: 600;
  line-height: normal;
}

.text-modal {
  text-align: center;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.contact-modal {
  margin-bottom: 18px;
}

.title-contact-modal {
  color: #5c5c5c;
  margin-bottom: 4px;
