.footer-container {
  max-width: 480px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  background: black;
}

.footer-nav-wrapper {
  display: flex;
  flex-direction: unset;
  justify-content: space-around;
  column-gap: 44px;
  align-items: baseline;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 32px 16px;
}
.footer-nav {
  flex-direction: column;
  justify-content: center;
  padding: 0;
  margin: 0;
  width: 128px;
}

.footer-nav-title {
  color: white;
  font-size: 18px;
  font-weight: bold;
  line-height: normal;
  margin-bottom: 18px;
  list-style: none;
}

.footer-nav-info {
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.02em;
  margin-bottom: 12px;
  color: white;
  list-style: none;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.link-active {
  text-decoration: none;
  color: white;
}

.footer-social-position {
  display: flex;
  flex-direction: row;
  width: 128px;
  padding: 0;
  gap: 14px;
}

.footer-social-link {
  width: 100%;
  height: 100%;
  background-color: transparent;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer-social-icon-color {
  color: white;
}

@media screen and (min-width: 768px) {
  .footer-container {
    max-width: 768px;
  }
  .footer-social-position {
    flex-direction: column;
    gap: 18px;
  }
  .footer-nav {
  }
}

@media screen and (min-width: 1158px) {
  .blog-container {
    max-width: 1158px;
  }
  .footer-container {
    max-width: 1158px;
  }
}
